import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { Placeholder } from "react-bootstrap";
import { ToastContainer, toast } from "react-toastify";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBasketShopping } from "@fortawesome/free-solid-svg-icons";
import styles from "./Nav.module.css";
import { typeList } from "../../actions/navActions";
import { setCategory } from "../../actions/navActions";

export default function Nav() {
  const dispatch = useDispatch();
  const navList = useSelector((state) => state.navState);
  const userInfo = useSelector((state) => state.userInfo);
  const { loading, types } = navList;

  const basketIcon = <FontAwesomeIcon icon={faBasketShopping} color="white" />;
  const toastContainer = (
    <ToastContainer
      position="bottom-right"
      autoClose={5000}
      hideProgressBar={false}
      newestOnTop={false}
      closeOnClick
      rtl={false}
      pauseOnFocusLoss
      draggable
      pauseOnHover
      theme="light"
    />
  );

  useEffect(() => {
    dispatch(typeList());
  }, []);

  const navClick = (typeId) => {
    const productsScroll = document.getElementById("products");
    dispatch(setCategory(typeId));
    productsScroll.scrollIntoView({
      behavior: "smooth",
      block: "start",
      inline: "start",
    });
  };

  const errorClick = () => {
    toast.error("!       ", {
      position: "bottom-right",
      autoClose: 4000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "dark",
    });
  };

  const navButtonPlaceholder = [];
  for (let i = 0; i < 8; i++) {
    navButtonPlaceholder.push(
      <Placeholder as="p" animation="glow" className={styles.placeholder}>
        <Placeholder xs={12} />
      </Placeholder>
    );
  }

  return (
    <>
      <div className={styles.nav}>
        {loading && navButtonPlaceholder}
        {/* {loading && <p>   </p>} */}
        {types &&
          types.map(({ nameUA, id }) => {
            return (
              <button
                onClick={() => {
                  navClick(id);
                }}
                id={id}
                key={nameUA}
                className={styles.categories}
              >
                {nameUA}
              </button>
            );
          })}

        {userInfo.token ? (
          <Link to="/basket" style={{ textDecoration: "none" }}>
            <button key="basketButtonKeyTrue" className={styles.basket}>
               {basketIcon}
            </button>
          </Link>
        ) : (
          <button
            key="basketButtonKeyFalse"
            className={styles.basket}
            onClick={errorClick}
          >
             {basketIcon}
          </button>
        )}
      </div>{" "}
      {toastContainer}
    </>
  );
}
