import React from "react";
import { Table } from "react-bootstrap";
import { useSelector } from "react-redux";

// import GooglePayButton from "@google-pay/button-react";
import styles from "./Receipt.module.css";

export default function Receipt() {
  const { currentOrderState } = useSelector((state) => state);
  // console.log(itemInOrder);

  if (currentOrderState.itemInOrder) {
    const {
      apartment,
      asap,
      basketId,
      change,
      code,
      comments,
      createdAt,
      date,
      email,
      firstName,
      floor,
      house,
      id,
      noChange,
      payment,
      phone,
      price,
      restaurant,
      status,
      street,
      time,
      updatedAt,
      voucher,
    } = currentOrderState.itemInOrder.orderInfo;
    const { items } = currentOrderState.itemInOrder;
    return (
      <div className={styles.container}>
        <div className={styles.table}>
          <h3>
                .   , 
            ,    .
          </h3>
        </div>

        <div className={styles.table}>
          <Table striped bordered hover responsive>
            <thead>
              <tr>
                <th>N</th>
                <th></th>
                <th></th>
                <th>  </th>
                <th></th>
                <th>  </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>
                  <b>{id}</b>
                </td>
                <td>
                  <b>{date}</b>
                </td>
                <td>{status}</td>
                <td>
                  <ul>
                    {firstName && (
                      <li>
                        <b>':</b> {firstName}
                      </li>
                    )}
                    {phone && (
                      <li>
                        <b>:</b> {phone}
                      </li>
                    )}
                    {email && (
                      <li>
                        <b>Email:</b> {email}
                      </li>
                    )}
                    {street && (
                      <li>
                        <b>:</b> {street}
                      </li>
                    )}
                    {restaurant ? (
                      <li>
                        <b>: </b>
                        {restaurant}
                      </li>
                    ) : (
                      ""
                    )}
                    {house && (
                      <li>
                        <b>:</b> {house}
                      </li>
                    )}
                    {apartment && (
                      <li>
                        <b>:</b> {apartment}
                      </li>
                    )}
                    {code && (
                      <li>
                        <b> :</b> {code}
                      </li>
                    )}
                    {floor && (
                      <li>
                        <b>:</b> {floor}
                      </li>
                    )}
                    {comments && (
                      <li>
                        <b>:</b> {comments}
                      </li>
                    )}
                    {time && (
                      <li>
                        <b> :</b>  {time}
                      </li>
                    )}
                     {asap === "on" && (
                      <li>
                        <b> </b>
                      </li>
                    )}

                    {date && (
                      <li>
                        <b> :</b> {date}
                      </li>
                    )}
                    {voucher && (
                      <li>
                        <b>:</b> {voucher}
                      </li>
                    )}
                  </ul>
                </td>
                <td>
                  <ul>
                    {items.map(({ product, quantity }) => {
                      const { price, nameUA } = product;

                      return (
                        <li>
                          {" "}
                          {nameUA} <i>{quantity}</i>{" "}
                        </li>
                      );
                    })}
                  </ul>
                </td>
                <td>
                  <ul>
                    {payment && (
                      <li>
                        <b> :</b> {payment}
                      </li>
                    )}

                    {price && (
                      <li>
                        <b> :</b> {price} 
                      </li>
                    )}
                  </ul>
                </td>
              </tr>
            </tbody>
          </Table>
        </div>
      </div>
    );
  }
}
