import React from "react";
import { Link } from "react-router-dom";
import styles from "./NotFound.module.css";

const NotFound = () => {
  return (
    <>
      <div className={styles.container}>
        <p className={styles.notFound}>404</p>
        <p>   </p>
        <p>       .</p>
        <div>
          <Link to="/"></Link>
        </div>
      </div>
    </>
  );
};

export default NotFound;
