import { useState } from "react";

import PhoneInput from "react-phone-number-input";

import styles from "./Delivery.module.css";

export default function Delivery() {
  const [phoneValue, setPhoneValue] = useState("+380");
  const [ASAP, setASAP] = useState(false);
  const [noChange, setNoChange] = useState(false);

  const asapInput = () => {
    if (ASAP) {
      setASAP(false);
    
    } else {
      setASAP(true);
        }
  };

  const noChangeInput = () => {
    if (noChange) {
      setNoChange(false);
    } else {
      setNoChange(true);
    }
  };

  let today = new Date();
  let dateDefaultValue = today.toISOString().substring(0, 10);
  let todayDeliveryMinutes = new Date(
    today.valueOf() + 30 * 60000
  ).getMinutes();
  let todayDeliveryHours = new Date(today.valueOf() + 30 * 60000).getHours();
  let timeDefaultValue = `${todayDeliveryHours}:${todayDeliveryMinutes}`;

  return (
    <div className={styles.container}>
      <div className={styles.block}>
        <label className={styles.label}>
           :
          <input
            type="text"
            name="firstName"
            placeholder="'"
            required
            className={styles.input}
            rules={{ required: true, length: 14 }}
          />
          <PhoneInput
            placeholder=""
            defaultCountry="UA"
            name="phone"
            value={phoneValue}
            onChange={setPhoneValue}
            className={styles.input}
            rules={{ required: true }}
            limitMaxLength
          />
          <input
            type="email"
            name="email"
            placeholder="Email ( ')"
            className={styles.input}
          />
        </label>
      </div>

      <div className={styles.block}>
        <label className={styles.label}>
          :{" "}
          <input
            type="text"
            name="street"
            placeholder=""
            required
            className={styles.input}
          />
          <input
            type="text"
            name="house"
            placeholder=""
            required
            className={styles.input}
          />
          <input
            type="text"
            name="apartment"
            placeholder=""
            required
            className={styles.input}
          />
          <br />
          <input
            type="text"
            name="code"
            placeholder=" ( ')"
            className={styles.input}
          />
          <input
            type="text"
            name="floor"
            placeholder=""
            required
            className={styles.input}
          />
          <input
            type="text"
            name="comments"
            placeholder=" ( ')"
            className={styles.input}
          />
        </label>
      </div>

      <div className={styles.block}>
        <label className={styles.label}>
          :{" "}
          {ASAP ? (
            <>
              <input
                type="date"
                name="date"
                placeholder=""
                className={styles.input}
                disabled
              />
              <input
                type="time"
                name="time"
                placeholder=""
                className={styles.input}
                disabled
              />
              <label className={styles.label}>
                {" "}
                <div className={styles.checkbox}>
                  <input
                    type="checkbox"
                    name="asap"
                    defaultValue="on"
                    onChange={asapInput}
                    checked
                  />
                  <div></div>
                </div>
              </label>
            </>
          ) : (
            <>
              <input
                type="date"
                name="date"
                placeholder=""
                className={styles.input}
                defaultValue={dateDefaultValue}
                min={dateDefaultValue}
              />
              <input
                type="time"
                name="time"
                placeholder=""
                className={styles.input}
                defaultValue={timeDefaultValue}
              />
              <label className={styles.label}>
                {" "}
                <div className={styles.checkbox}>
                  <input
                    type="checkbox"
                    name="asap"
                    defaultValue="off"
                    onChange={asapInput}
                  />
                  <div></div>
                </div>
              </label>
            </>
          )}
        </label>
      </div>

      <div className={styles.block}>
        <label className={styles.label}>
          :{" "}
          <input
            type="text"
            name="voucher"
            placeholder="   ( ')"
            className={styles.input}
          />
          {noChange ? (
            <>
              <input
                type="number"
                name="change"
                placeholder=" "
                className={styles.input}
                disabled
              />{" "}
              <label className={styles.label}>
                 
                <div className={styles.checkbox}>
                  <input
                    type="checkbox"
                    name="noChange"
                    value="on"
                    onInput={noChangeInput}
                    checked
                  />
                  <div></div>
                </div>
              </label>
            </>
          ) : (
            <>
              <input
                type="number"
                name="change"
                placeholder=" "
                className={styles.input}
              />{" "}
              <label className={styles.label}>
                 
                <div className={styles.checkbox}>
                  <input
                    type="checkbox"
                    name="noChange"
                    value="off"
                    onInput={noChangeInput}
                  />
                  <div></div>
                </div>
              </label>
            </>
          )}
          <select name="payment" className={styles.input}>
            <option value="card">  </option>
          </select>
        </label>
      </div>
    </div>
  );
}
